body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
*{
  margin: 0px;
  padding: 0px;
}













@font-face {
  font-family: 'Gabi';
  src: url('./fonts/GabrielaStencil.otf') format('opentype');
  font-style: normal;
  font-weight: normal;
}

@font-face {
  font-family: 'Avenir';
  src: url('./fonts/AvenirNext.otf') format('truetype');
  font-style: normal;
  font-weight: normal;
}



code {
  font-family: Gabi, Avenir ,source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
